import React from "react"

export const input = (
  type,
  id,
  name,
  list,
  minLength,
  maxLength,
  placeholder,
  handleInput,
  value
) => (
  <input
    type={type}
    id={id}
    name={name}
    list={list}
    minLength={minLength}
    maxLength={maxLength}
    placeholder={placeholder}
    autoComplete="on"
    required
    onChange={handleInput}
    onClick={handleInput}
    value={value}
  />
)

export const select = (
  id,
  name,
  minLength,
  maxLength,
  handleChange,
  value,
  children
) => (
  <select
    id={id}
    name={name}
    minLength={minLength}
    maxLength={maxLength}
    autoComplete="on"
    required
    onClick={handleChange}
    onBlur={handleChange}
    onChange={handleChange}
    value={value}
  >
    {children}
  </select>
)

export const selection = (
  id,
  name,
  minLength,
  maxLength,
  handleChange,
  value,
  children
) => (
  <select
    id={id}
    name={name}
    minLength={minLength}
    maxLength={maxLength}
    autoComplete="on"
    required
    multiple
    onBlur={handleChange}
    onClick={handleChange}
    onChange={handleChange}
    value={value}
  >
    {children}
  </select>
)
