import React from "react"
import { connect } from "react-redux"

import styles from "../../styles/budget/tools.module.scss"

import { tableTypes } from "../../types/budget/types"
import {
  TRANSACTIONS_ID,
  ANALYSIS_ID,
  STRUCTURE_ID,
  ACCOUNTS_ID,
  // INCOME_ID,
} from "../../models/budget/tables"

const Table = ({
  headings,
  inputs,
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
}) => {
  const table = headings.toString()
  const cols = headings.length
  let rows = 0

  const getRows = tableLength => tableLength / cols

  switch (table) {
    case TRANSACTIONS_ID:
      rows = getRows(transactions.length)
      break
    case ANALYSIS_ID:
      rows = getRows(analysis.length)
      break
    case STRUCTURE_ID:
      rows = getRows(portfolio.length)
      break
    case ACCOUNTS_ID:
      rows = getRows(setup.length)
      break
    default:
      rows = getRows(calculator.length)
  }

  let thead
  let tbody
  let tableRows = []

  const setTableHead = () => {
    const list = headings.map((heading, key) => (
      <th scope="col" key={key}>
        {heading}
      </th>
    ))
    thead = (
      <thead>
        <tr key={rows}>{list}</tr>
      </thead>
    )
  }

  const createRow = (key, row) => {
    return <tr key={key}>{row}</tr>
  }

  const addRows = inputList => {
    const list = inputList.map((input, key) => {
      const label = headings[key % cols]
      return (
        <td data-label={label} key={key}>
          {input}
        </td>
      )
    })

    let row = 0
    let newRow = []
    for (const input in list) {
      newRow.push(list[input])
      if (newRow.length === cols) {
        row += 1
        tableRows.push(createRow(row, newRow))
        newRow = []
      }
    }
  }

  const setTableBody = () => {
    addRows(inputs)
    tbody = <tbody>{tableRows}</tbody>
  }

  setTableHead()
  setTableBody()

  return (
    <>
      <table className={styles.table}>
        {thead}
        {tbody}
      </table>
    </>
  )
}

Table.propTypes = tableTypes

const mapStateToProps = ({
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
}) => {
  return { calculator, portfolio, setup, transactions, analysis }
}
// const mapDispatchToProps = dispatch => {
//   return { clear: text => dispatch(resizeSession(text)) }
// }

// const ConnectedTable = connect(mapStateToProps, mapDispatchToProps)(Table)
const ConnectedTable = connect(mapStateToProps, null)(Table)

export default ConnectedTable
