import { connect } from "react-redux"

import {
  TRANSACTIONS_ID,
  ANALYSIS_ID,
  STRUCTURE_ID,
  ACCOUNTS_ID,
  // INCOME_ID,
} from "../../models/budget/tables"

import {
  updateCalculator,
  updatePortfolio,
  updateSetup,
  updateTransactions,
  updateAnalysis,
} from "../../state/actionTypes"

import { selectionTypes } from "../../types/budget/types"
import { selection } from "../../HTML/tags"

const Selection = ({
  handleChange,
  id,
  table,
  index,
  name,
  minLength,
  maxLength,
  children,
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
  newTransactions,
  newSetup,
  newAnalysis,
  newCalculator,
  newPortfolio,
}) => {
  const handleInput = e => {
    const getOptions = e => {
      value = e.target.options
      const options =
        value != null
          ? Array.from(e.target.options)
              .filter(i => i.selected)
              .map(i => i.value)
          : undefined
      return options
    }

    switch (table) {
      case TRANSACTIONS_ID:
        newTransactions(getOptions(e), index)
        break
      case ANALYSIS_ID:
        newAnalysis(getOptions(e), index)
        break
      case STRUCTURE_ID:
        newSetup(getOptions(e), index)
        break
      case ACCOUNTS_ID:
        newPortfolio(getOptions(e), index)
        break
      default:
        newCalculator(getOptions(e), index)
    }
    handleChange(e)
  }

  let value = []

  switch (table) {
    case TRANSACTIONS_ID:
      value = transactions[index]
      break
    case ANALYSIS_ID:
      value = analysis[index]
      break
    case STRUCTURE_ID:
      value = setup[index]
      break
    case ACCOUNTS_ID:
      value = portfolio[index]
      break
    default:
      value = calculator[index]
  }

  return selection(id, name, minLength, maxLength, handleInput, value, children)
}

Selection.propTypes = selectionTypes

const mapStateToProps = ({
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
}) => {
  return { calculator, portfolio, setup, transactions, analysis }
}
const mapDispatchToProps = dispatch => {
  return {
    newTransactions: (input, index) =>
      dispatch(updateTransactions(input, index)),
    newSetup: (input, index) => dispatch(updateSetup(input, index)),
    newAnalysis: (input, index) => dispatch(updateAnalysis(input, index)),
    newCalculator: (input, index) => dispatch(updateCalculator(input, index)),
    newPortfolio: (input, index) => dispatch(updatePortfolio(input, index)),
  }
}

const ConnectedSelection = connect(
  mapStateToProps,
  mapDispatchToProps
)(Selection)

export default ConnectedSelection
