import { connect } from "react-redux"

import {
  TRANSACTIONS_ID,
  ANALYSIS_ID,
  STRUCTURE_ID,
  ACCOUNTS_ID,
  // INCOME_ID,
} from "../../models/budget/tables"

import {
  updateCalculator,
  updatePortfolio,
  updateSetup,
  updateTransactions,
  updateAnalysis,
} from "../../state/actionTypes"

import { inputTypes } from "../../types/budget/types"
import { input } from "../../HTML/tags"

const NumberInput = ({
  handleChange,
  type,
  id,
  table,
  index,
  name,
  list,
  minLength,
  maxLength,
  placeholder,
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
  newTransactions,
  newSetup,
  newAnalysis,
  newCalculator,
  newPortfolio,
}) => {
  const handleInput = e => {
    handleChange(e)
    switch (table) {
      case TRANSACTIONS_ID:
        newTransactions(e.target.value, index)
        break
      case ANALYSIS_ID:
        newAnalysis(e.target.value, index)
        break
      case STRUCTURE_ID:
        newSetup(e.target.value, index)
        break
      case ACCOUNTS_ID:
        newPortfolio(e.target.value, index)
        break
      default:
        newCalculator(e.target.value, index)
    }
  }

  let value

  switch (table) {
    case TRANSACTIONS_ID:
      value = transactions[index]
      break
    case ANALYSIS_ID:
      value = analysis[index]
      break
    case STRUCTURE_ID:
      value = setup[index]
      break
    case ACCOUNTS_ID:
      value = portfolio[index]
      break
    default:
      value = calculator[index]
  }

  return input(
    type,
    id,
    name,
    list,
    minLength,
    maxLength,
    placeholder,
    handleInput,
    value
  )
}

NumberInput.propTypes = inputTypes

const mapStateToProps = ({
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
}) => {
  return { calculator, portfolio, setup, transactions, analysis }
}
const mapDispatchToProps = dispatch => {
  return {
    newTransactions: (input, index) =>
      dispatch(updateTransactions(input, index)),
    newSetup: (input, index) => dispatch(updateSetup(input, index)),
    newAnalysis: (input, index) => dispatch(updateAnalysis(input, index)),
    newCalculator: (input, index) => dispatch(updateCalculator(input, index)),
    newPortfolio: (input, index) => dispatch(updatePortfolio(input, index)),
  }
}

const ConnectedNumberInput = connect(
  mapStateToProps,
  mapDispatchToProps
)(NumberInput)

export default ConnectedNumberInput
