import React from "react"
import { connect } from "react-redux"
import TextInput from "./TextInput"
import DateInput from "./DateInput"
import NumberInput from "./NumberInput"
import Select from "./Select"
import Selection from "./Selection"
import { inputTypes } from "../../types/budget/types"

import {
  resizeCalculator,
  resizePortfolio,
  resizeSetup,
  resizeTransactions,
  resizeAnalysis,
} from "../../state/actionTypes"

import {
  TRANSACTIONS_ID,
  ANALYSIS_ID,
  STRUCTURE_ID,
  ACCOUNTS_ID,
  INCOME_ID,
} from "../../models/budget/tables"

const Input = ({
  id,
  index,
  type,
  name,
  placeholder,
  eventHandler,
  minSize,
  maxSize,
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
  multipleSelection,
  updateCalculator,
  updatePortfolio,
  updateSetup,
  updateTransactions,
  updateAnalysis,
}) => {
  const reducer = (accumulator, currentValue) =>
    accumulator + currentValue.charCodeAt(0)
  const code = id.split("").reduce(reducer, 0)

  let table
  let handleTable
  let normaliseRow
  let key
  switch (id) {
    case TRANSACTIONS_ID:
      key = 1
      table = transactions
      handleTable = updateTransactions
      normaliseRow = row => {
        return row
      }
      break
    case ANALYSIS_ID:
      key = 2
      table = analysis
      handleTable = updateAnalysis
      normaliseRow = row => {
        return row
      }
      break
    case STRUCTURE_ID:
      key = 2
      table = setup
      handleTable = updateSetup
      normaliseRow = row => {
        return row
      }
      break
    case ACCOUNTS_ID:
      key = 3
      table = portfolio
      handleTable = updatePortfolio
      normaliseRow = row => {
        return row
      }
      break
    case INCOME_ID:
      key = 2
      table = calculator
      handleTable = updateCalculator
      normaliseRow = row => {
        return row
      }
      break
    default:
      key = 2
      table = calculator
      handleTable = updateCalculator
      normaliseRow = row => {
        return row
      }
  }

  const extendTable = (arr, key) => {
    let extend = true
    let counter = arr.length - 1
    while (counter > -1 && extend) {
      if (
        arr[counter] === null ||
        arr[counter] === undefined ||
        arr[counter].toString().trim() === ""
      ) {
        extend = false
      }
      counter -= 1
    }
    if (extend === true) {
      const slice = arr.slice(-4)
      slice[key] = undefined
      table = normaliseRow(arr.concat(slice))
      return true
    }
    return false
  }

  const contractTable = arr => {
    let counter = arr.length - 5
    let contract = false
    while (table.length > 8 && counter > 7 && !contract) {
      if (
        arr[counter] === null ||
        arr[counter] === undefined ||
        arr[counter].toString().trim() === "" ||
        arr[counter - 1] === null ||
        arr[counter - 1] === undefined ||
        arr[counter - 1].toString().trim() === "" ||
        arr[counter - 2] === null ||
        arr[counter - 2] === undefined ||
        arr[counter - 2].toString().trim() === "" ||
        arr[counter - 3] === null ||
        arr[counter - 3] === undefined ||
        arr[counter - 3].toString().trim() === ""
      ) {
        contract = true
        // remove section
        table = arr.slice(0, counter - 3).concat(arr.slice(counter + 1))
      }
      counter -= 4
    }
    return contract
  }

  const handleInputEvents = e => {
    eventHandler(e)
    if (name !== "Session ID") {
      if (extendTable(table, key)) {
        handleTable(table)
      }

      if (contractTable(table)) {
        handleTable(table)
      }
    }
  }

  let input
  let list

  const createList = () => {
    list = placeholder.map((listItem, key) => {
      return <option value={listItem} key={key} />
    })
  }

  const createInput = () => {
    input = (
      <>
        {input}
        {list}
      </>
    )
  }

  const setDataList = () => {
    createList()
    list = <datalist id={`${name}_list_${code}`}>{list}</datalist>
  }

  const setList = () => {
    list = placeholder.map((listItem, key) => {
      return (
        <option value={listItem} key={key}>
          {listItem}
        </option>
      )
    })
  }

  const setSelection = () => {
    setList()
    input = multipleSelection ? (
      <Selection
        table={id}
        index={index}
        id={`${name}_${index}${code}`}
        name={name}
        minLength={minSize}
        maxLength={maxSize}
        handleChange={handleInputEvents}
      >
        {list}
      </Selection>
    ) : (
      <Select
        table={id}
        index={index}
        id={`${name}_${index}${code}`}
        name={name}
        minLength={minSize}
        maxLength={maxSize}
        handleChange={handleInputEvents}
      >
        {list}
      </Select>
    )
  }

  const setTextInput = () => {
    setDataList()
    input = (
      <TextInput
        type={type}
        table={id}
        index={index}
        id={`${name}_${index}${code}`}
        name={name}
        list={`${name}_list_${code}`}
        minLength={minSize}
        maxLength={maxSize}
        placeholder={placeholder[0]}
        handleChange={handleInputEvents}
      />
    )
    createInput()
  }

  const setDateInput = () => {
    setDataList()
    input = (
      <DateInput
        type={type}
        table={id}
        index={index}
        id={`${name}_${index}${code}`}
        name={name}
        list={`${name}_list_${code}`}
        minLength={minSize}
        maxLength={maxSize}
        placeholder={placeholder[0]}
        handleChange={handleInputEvents}
      />
    )
    createInput()
  }

  const setNumberInput = () => {
    setDataList()
    input = (
      <NumberInput
        type={type}
        table={id}
        index={index}
        id={`input_${index}`}
        name={name}
        list={`${name}_list_${code}`}
        minLength={minSize}
        maxLength={maxSize}
        placeholder={placeholder[0]}
        handleChange={handleInputEvents}
      />
    )
    createInput()
  }

  switch (type) {
    case "select":
      setSelection()
      break
    case "date":
      setDateInput()
      break
    case "number":
      setNumberInput()
      break
    default:
      setTextInput()
  }

  return input
}

Input.defaultProps = {
  id: "Session",
  type: "text",
  name: "HW",
  index: 0,
  placeholder: ["Hello", "World"],
  eventHandler: e => console.log("PengCutz.com"),
  minSize: "10px",
  maxSize: "50px",
  multipleSelection: false,
  value: "HW!",
}

Input.propTypes = inputTypes

const mapStateToProps = ({
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
}) => {
  return { calculator, portfolio, setup, transactions, analysis }
}
const mapDispatchToProps = dispatch => {
  return {
    updateCalculator: arr => dispatch(resizeCalculator(arr)),
    updatePortfolio: arr => dispatch(resizePortfolio(arr)),
    updateSetup: arr => dispatch(resizeSetup(arr)),
    updateTransactions: arr => dispatch(resizeTransactions(arr)),
    updateAnalysis: arr => dispatch(resizeAnalysis(arr)),
  }
}

const ConnectedInput = connect(mapStateToProps, mapDispatchToProps)(Input)

export default ConnectedInput

